import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, Platform, ViewController } from 'ionic-angular';
import { Calendar, CalendarOptions } from '@ionic-native/calendar';
import { Storage } from '@ionic/storage';
import { User }from '../../providers';
import { FormBuilder, FormGroup, Validators } from '../../../node_modules/@angular/forms';



@IonicPage()
@Component({
  selector: 'page-add-task',
  templateUrl: 'add-task.html',
})
export class AddTaskPage {

  public event = { user: 0, folio: 0, subject: "", startDate: "", startTime: "", endDate: "", endTime: "", reDate: "", reTime: "", descripcion: "" };

  contactUser: FormGroup;
  public client = false;
  public nameClient = '';
  public date: any;
  public time: any;
  public myDate: any;
  public monthName: any;
  public shortMonthName: any;
  public subjet: any = [];
  public calendars: any;
  public calendarId = 2;
  public phoneCalendar = false;

  public leadForm: FormGroup;

  constructor(public formBuilder: FormBuilder, public navCtrl: NavController,    public navParams: NavParams,    private storage: Storage,    public alertCtrl: AlertController,    private calendar: Calendar,    public platform: Platform,private user: User,public viewCtrl: ViewController) {

    
    this.event.user = this.user.userInfo.userid;

    this.myDate = new Date().toISOString();


    this.date = this.myDate.substring(0, 10);
    this.time = this.myDate.substring(11, 16);
    this.event.startDate = this.myDate.substring(0, 10);


    this.monthName = ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'];
    this.shortMonthName = ['Ene.', 'Feb.', 'Mar.', 'Abr.', 'May.', 'Jun.', 'Jul.', 'Ago.', 'Sep.', 'Oct.', 'Nov.', 'Dic.'];
    this.subjet = ['0', 'Cita', 'Llamada', 'Guardia', 'Vista de la Propiedad', 'InspecciÃ³n', 'Domiciliaria', 'Otros', 'Seguimiento'];

    this.platform.ready().then(() => {
      this.calendar.listCalendars().then(data => {

        this.calendars = data;

        this.calendars.forEach((value, key, index) => {
          if (value.isPrimary) {
            this.calendarId = value.id;
          }
        });


        //console.log(this.calendars);
        //console.log(JSON.stringify(this.calendars));
      }).catch(() => {

      } )
    })


    this.leadForm = formBuilder.group({
      subject: ['', [Validators.required]],
      startDate: ['', [Validators.required]],
      startTime: ['', [Validators.required]],
      sinc: [''],
      endDate: ['', [Validators.required]],
      endTime: ['', [Validators.required]],
      reDate: ['', [Validators.required]],
      reTime: ['', [Validators.required]],
      descripcion: ['', [Validators.required]]

    });


  }
  valid(){

  
    if(this.leadForm.valid && this.nameClient != ''){
      return false;
    }else{

      return true;

    }
  }
    ionViewDidLoad() {
    //console.log('ionViewDidLoad AddTaskPage');
  } 


  save() {

    //console.log(this.phoneCalendar);

    var body = '';
    var $this = this;
    Object.keys(this.event).forEach(function (key) {
      body += key + '=' + $this.event[key] + '&';
    });
    body += 'm=addTask&token=' + this.user.userToken;

    //console.log(body);

    var promise = this.user.addTask(body);

    Promise.all([promise]).then(data => {


      if(data[0]){

        var opt: CalendarOptions = {
          calendarId: this.calendarId,
          firstReminderMinutes: 60,
          id: this.calendarId + ""
        };

        var opts = { calendarId: this.calendarId, firstReminderMinutes: 120, id: this.calendarId + "" };

        if (this.phoneCalendar) {
          this.calendar.createEventInteractivelyWithOptions(this.subjet[this.event.subject], this.nameClient, this.event.descripcion, new Date(this.event.startDate), new Date(this.event.endDate), opts).then(
            (msg) => {

              let alert = this.alertCtrl.create({
                title: 'Ãxito',
                subTitle: 'La tarea se agendo correctamente',
                buttons: ['OK']
              });
              alert.present();
              this.viewCtrl.dismiss({ data: 'reload' });

            },
            (err) => {
              let alert = this.alertCtrl.create({
                title: 'Ãxito',
                subTitle: 'La tarea se agendo correctamente en el sistema, pero tuvimos problemas al sincronizar con el calendario de tu telÃ©fono.',
                buttons: ['OK']
              });
              alert.present();
              this.viewCtrl.dismiss({ data: 'reload' });
            }
          );
        } else {

          let alert = this.alertCtrl.create({
            title: 'Ãxito',
            subTitle: 'La tarea se agendo correctamente',
            buttons: ['OK']
          });
          alert.present();
          this.viewCtrl.dismiss({data : 'reload'});
        }




      }else{
        let alert = this.alertCtrl.create({
          title: 'Error',
          subTitle: 'OcurriÃ³ un problema, intenta mas tarde.',
          buttons: ['OK']
        });

        alert.present();
        this.viewCtrl.dismiss({ data: 'reload' });
      }
   


    });

  }




  goCliente() {

    this.navCtrl.push('LeadPickeadPage', 'lead').then(() => {

      this.navCtrl.getActive().onDidDismiss(data => {
        //console.log(data);
        if (data) {
          //console.log('si');
          this.client = true;
          this.nameClient = data.fullname;
          this.event.folio = data.folio;
        } else {
          //console.log('no');
          this.client = false;
        }
      });
    });

  }

  close() {

    let alert = this.alertCtrl.create({
      title: 'AtenciÃ³n',
      message: 'Se perderÃ¡ la informaciÃ³n del formulario',
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',
        },
        {
          text: 'Salir',
          handler: () => {

            this.viewCtrl.dismiss({ data: 'reload' });
          }
        }
      ]
    });

    alert.present();

  }

}



